import * as React from 'react';

// MUI Components 
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';

// Page Components
import TabPanel from './components/TabPanel2';

// react-icons 
import { FaCog } from 'react-icons/fa';
import { FcServices } from 'react-icons/fc';

export default function AdminSettingsTabs() {

    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box
            sx={{ display: 'flex', flexGrow: 1, flexDirection: 'row', overflow: 'hidden' }}
        >
            <Box>
                <Box display="flex" flexDirection="row" sx={{ p: 1, ml: 0, mr: -10, width: 220, backgroundColor: '#eee' }}>
                    <FcServices size={25} style={{ marginRight: 5, marginTop: 2, marginLeft: 5 }} />
                    <Typography variant="h6">
                        Settings
                    </Typography>
                </Box>
                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    sx={{ height: '100vh', flexGrow: 1, width: 220, pt: 0, borderRight: '2px solid #eee' }}
                    TabIndicatorProps={{
                        style: {
                            right: 1,
                            height: 30,
                            marginTop: 10
                        }
                    }}
                >
                    <Tab onClick={() => handleChange(0)} sx={{ alignItems: "start" }} label={
                        <Typography variant="subtitle2">
                            <Box display="flex">
                                <FaCog size={20} style={{ marginRight: 5 }} />
                                Settings
                            </Box>
                        </Typography>
                    } />
                </Tabs>
            </Box>
            <Box display="flex" flexGrow={1}>
                <TabPanel value={value} index={0}>
                    <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                        Settings
                    </Paper>
                </TabPanel>
            </Box>
        </Box>
    );
}
